<template>
  <div>
    <nav class="navbar navbar-inverse navbar-fixed-top">
      <div class="container-fluid">
        <div class="navbar-header">
          <button
            type="button"
            class="navbar-toggle collapsed"
            data-toggle="collapse"
            data-target="#navbar"
            aria-expanded="false"
            aria-controls="navbar"
          >
            <span class="sr-only">Toggle navigation</span>
            <span class="icon-bar"></span>
            <span class="icon-bar"></span>
            <span class="icon-bar"></span>
          </button>
          <router-link to="/" class="navbar-brand"></router-link>
        </div>
        <div id="navbar" class="navbar-collapse collapse">
          <ul class="nav navbar-nav navbar-right">
            <template v-if="user.logined">
              <li>
                <a>,{{ user.humanname }}</a>
              </li>
              <li>
                <a v-on:click="logout" href="/"></a>
              </li>
            </template>
            <li>
              <router-link v-if="!user.logined" to="/logIn"></router-link>
            </li>
            <li>
              <router-link to="/about"></router-link>
            </li>
          </ul>
        </div>
      </div>
    </nav>
  </div>
</template>

<script>
import vuex, { mapState, mapMutations } from "vuex";

export default {
  computed: {
    ...mapState(["user"])
  },
  methods: {
    logout() {
      let hint = this.$store.state.closeHint;
      if (!hint || (hint && confirm(hint))) {
        this.$store.commit("logout");
      }
    }
  }
};
</script>

