<!--  -->
<template>
  <div style="position:fixed; left:0px; top:48px; width:100%; height:100%;background: #222;">
    <div class="container form-regist">
      <form id="regist-form" autocomplete="off" method="get">
        <h3></h3>
        <div class="input-group">
          <span class="input-group-addon">
            <i class="fa fa-user" aria-hidden="true"></i>
          </span>
          <input type="text" class="form-control" placeholder="" v-model="username" />
        </div>
        <div class="input-group">
          <span class="input-group-addon">
            <i class="fa fa-lock" aria-hidden="true"></i>
          </span>
          <input type="password" class="form-control" placeholder="" v-model="password" />
        </div>
        <div class="form-group">
          <button type="submit" id="login_button" class="btn btn-primary" v-on:click="login">
            <i class="glyphicon glyphicon-ok"></i> 
          </button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      username: "",
      password: ""
    };
  },
  methods: {
    login(e) {
      this.$axios
        .get("/login", {
          params: {
            username: this.username,
            password: this.password
          }
        })
        .then(resp => {
          if (resp.data.result) {
            this.$store.commit("login", {
              token: resp.data.token,
              name: resp.data.name,
              power: resp.data.power
            });
            const redirect = this.$route.params.redirect;
            if (redirect) this.$router.push(redirect);
            else this.$router.push("/");
          } else {
            alert("" + resp.data.info);
          }
        })
        .catch(err => {
          alert("" + err);
        });

      e.preventDefault();
    }
  }
};
</script>
<style scoped>
h3 {
  margin-top: 10%;
  color: white;
}

#regist-form {
  max-width: 400px;
  margin: 0 auto;
}

.input-group {
  margin-top: 20px;
  margin-bottom: 20px;
}

@media (min-width: 800px) {
  #regist-form {
    width: 400px;
    float: right;
  }

  #login_button {
    float: right;
  }
}

@media (max-width: 799px) {
  #login_button {
    width: 100%;
  }
}
</style>
